import React, {useState, useEffect} from 'react'
import './AllServicePage.scss'
import data from '../LandingPage/all3.json'
import Button from '@material-ui/core/Button';
import {Radio} from 'antd';
import ResultCard2 from '../../tools/ResultCard2'

function AllServicePage() {
    const [types,setTypes] = useState([])
    const [type,setType] = useState("")
    const [services,setServices] = useState([])
    const [searchKey,setSearchKey] = useState("")
    const [nums,setNums] = useState({})

    useEffect(() => {
        let temp = []
        let t = {}
        temp.push("Ï†ÑÏ≤¥")
        for(const d of data){
            if(temp.includes(d.Type)){
                t[d.Type] += 1
               continue
            }
            temp.push(d.Type)
            t[d.Type] = 1
        }
        setTypes(temp)
        setNums(t)

        if(type === "Ï†ÑÏ≤¥"){
            setServices(data)
        }else{
            let td = data;
            td = td.filter(t => t.Type === type)
            setServices(td)
        }
        console.log(t)
    },[type])



    const typesTable = types.map((t, index) => {
        let bcolor = 'rgba(255, 255, 255, 1)';

        if(type === t){
            bcolor = 'rgb(2, 153, 22)';
        }

        return (
            <span key={index} style={{ backgroundColor:`${bcolor}`, borderWidth:2, borderColor:'red', }} onClick={e => {e.preventDefault(); setType(t);}} className="typebuttons">
                {t + "  "}
                {nums[t]}
            </span>
        )
    });

    const serviceReturn = services.map((s, index) => {
        return (
            <div style={{marginTop:'5%', display: 'flex', justifyContent:'center'}}>
                <ResultCard2 key={index} data={s} style={{margin:20}}/>
            </div>
        )
    })

    const doSearch = () => {
        let temp = []
        if(type === "Ï†ÑÏ≤¥"){
            temp = data.filter(t => t.Desc.includes(searchKey) || t.Situation.includes(searchKey) )
        }else{
           temp = data.filter(t => t.Desc.includes(searchKey) && t.Type===type)
        }
        setServices(temp)
    }

    return (
        <div className="Container">
            <div className="buttonsWrapper">
                <p className="selectText">ÌÉÄÏûÖ ÏÑ†ÌÉùÌïòÍ∏∞</p>
                {typesTable}
            </div>
            <div>
                <input type="text" className="searchInput" value={searchKey} onChange={e => setSearchKey(e.target.value)} placeholder="Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"/>
                <Button onClick={doSearch} style={{backgroundColor:'green', color:'white', marginLeft:10}}>
                    Í≤ÄÏÉâ
                </Button>
            </div>
            <div className="servicesWrapper">
                <span>
                {serviceReturn}
                </span>
            </div>
        </div>
    )
}

export default AllServicePage
